import React from 'react';
import Player from './Player';
import './Players.css';

const samples = (
    <React.Fragment>
        <Player src="https://static.bandlab.com/soundbanks/previews/new-wave-kit.ogg" caption="Drums" />
        <Player src="https://static.bandlab.com/soundbanks/previews/synth-organ.ogg" caption="Melody" />
    </React.Fragment>
)

const sequencer = (
    <React.Fragment>
        <Player src="//mshushakov.github.io/audio/shaker.ogg" caption="Shaker" sync="120/4" />
        <Player src="//mshushakov.github.io/audio/drums.ogg" caption="Drums" sync="120/8" />
        <Player src="//mshushakov.github.io/audio/bass.ogg" caption="Bass" gain=".6" sync="120/16" />
        <Player src="//mshushakov.github.io/audio/chords.ogg" caption="Chords" gain=".6" sync="120/16" />
        <Player src="//mshushakov.github.io/audio/lead.ogg" caption="Lead"  gain=".7" sync="120/16" />
        <Player src="//mshushakov.github.io/audio/synth.ogg" caption="Synth" gain=".6" sync="120/16" />
        <Player src="//mshushakov.github.io/audio/synth2.ogg" caption="Synth" gain=".2" sync="120/16" />
    </React.Fragment>
)

export default function Players(props) {
	return (
        <div className="players">
            { props.sequencer ? sequencer : samples }
        </div>
    )
}


